const validasiPembayaranSupplier = (value) => {
    const errors = {};
    if (!value.tanggal) {
      errors.tanggal = "Tidak Boleh kosong";
    }
    if (!value.supplier) {
      errors.supplier = "Tidak Boleh kosong";
    }
    if (!value.noBon) {
      errors.noBon = "Tidak Boleh kosong";
    }
   
    return errors;
  };
  
  export default validasiPembayaranSupplier;
  