import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import {
  setTampungCiok,
  settampungRongsok,
} from "../actions/pembayaranSupplierAction";
import Swal from "sweetalert2";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { setCashTemp, setTransferTemp } from "../actions/customerAction";
import { renderField, renderFieldSelect } from "../renderField";
import Skeleton from "react-loading-skeleton";
import validasiPembayaranSupplier from "../Validations/validasiPembayaranSupplier";
import { getToday } from "../getDate";
import { finishSend, progressSend } from "../actions/LogicAction";
import { getsupplierList } from "../actions/supplierAction";

const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const FormCiok = lazy(() => import("./Component/FormCiok"));
const FormRongsok = lazy(() => import("./Component/FormRongsok"));
const AccordCash = lazy(() => import("./Element/AccordCash"));
const AccordTrannsfer = lazy(() => import("./Element/AccordTransfer"));
const AccordCiok = lazy(() => import("./Element/AccordCiok"));
const AccordRongsok = lazy(() => import("./Element/AccordRongsok"));
const AccordCashMurni = lazy(() => import("./Element/AccordCashMurni"));
const AccordTransferMurni = lazy(() => import("./Element/AccordTransferMurni"));
const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

class AmbilTitipanSupplierHead extends Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    this.state = {
      listSupplier: [],
      tanggal: formatedDate,
      bonBayar: "",
      detail: "",
      tanggalInvoice: "",
      noPembayaran: "",
      supplier: "",
      hutangnya: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
    this.props.dispatch(setTampungCiok());
    this.props.dispatch(settampungRongsok());

    this.props.change("tanggal", getToday);
    this.props.change("totalBayar", this.props.totalBayar);
    this.props.change("totalBayarCash", this.props.totalBayarRp);
    // this.props.change("totalBayarCash", this.props.totalBayarTunai);
    this.setTotalBayar();
  }
  // SAVE CIOK FROM MODAL
  handleSubmit(data) {
    var kode = data.kodeBarang;
    var split = kode.split("|");
    var isi = {
      kode_jenis: split[0],
      netto: data.netto,
      bruto: data.bruto,
      kadar_transaksi: data.kadarTransaksi,
    };
    var print = {
      jenis: "CIOK",
      gross: data.bruto,
      harga: "-",
      kadar: data.kadarTransaksi,
      netto: parseFloat(data.netto).toFixed(3),
      pkg: "",
      gwpkg: "",
    };
    this.props.dispatch(progressSend());
    var sebelum =
      JSON.parse(localStorage.getItem("PrintPembayaranSupplier")) || [];
    sebelum.push(print);
    localStorage.setItem("PrintPembayaranSupplier", JSON.stringify(sebelum));
    var ciokTemp = JSON.parse(localStorage.getItem("ciokTemp")) || [];
    ciokTemp.push(isi);
    localStorage.setItem("ciokTemp", JSON.stringify(ciokTemp));
    var alatBayarBarang =
      JSON.parse(localStorage.getItem("alatBayarCiok")) || [];
    alatBayarBarang.push(isi);
    localStorage.setItem("alatBayarCiok", JSON.stringify(alatBayarBarang));
    this.props.dispatch(setTampungCiok());
    this.setTotalBayar();
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    }).then(() => this.props.dispatch(finishSend()));
  }
  // AKHIR SAVE CIOK FROM MODAL
  // SAVE RONGSOK FROM MODAL
  handleSubmit2(data) {
    var kode = data.kodeBarang;
    var split = kode.split("|");
    var isi = {
      kode_jenis: split[0],
      netto: data.netto,
      bruto: data.bruto,
      kadar_transaksi: data.kadarTransaksi,
    };
    var print = {
      jenis: "RONGSOK",
      gross: data.bruto,
      harga: "-",
      kadar: data.kadarTransaksi,
      netto: parseFloat(data.netto).toFixed(3),
      pkg: "",
      gwpkg: "",
    };
    this.props.dispatch(progressSend());
    var sebelum =
      JSON.parse(localStorage.getItem("PrintPembayaranSupplier")) || [];
    sebelum.push(print);
    localStorage.setItem("PrintPembayaranSupplier", JSON.stringify(sebelum));
    var rongsokTemp = JSON.parse(localStorage.getItem("rongsokTemp")) || [];
    rongsokTemp.push(isi);
    localStorage.setItem("rongsokTemp", JSON.stringify(rongsokTemp));
    var alatBayarBarang =
      JSON.parse(localStorage.getItem("alatBayarRosok")) || [];
    alatBayarBarang.push(isi);
    localStorage.setItem("alatBayarRosok", JSON.stringify(alatBayarBarang));
    this.props.dispatch(settampungRongsok());
    this.setTotalBayar();
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    }).then(() => this.props.dispatch(finishSend()));
  }
  // AKHIR SAVE RONGSOK FROM MODAL
  setCiok() {
    this.props.change("ciokBrutto", this.props.totalCiok);
    this.props.change("ciokNetto", this.props.totalCiok);
    this.setTotalBayar();
  }
  setTotal = (input) => (e) => {
    localStorage.setItem([input], e.target.value);
    this.setTotalBayar();
  };
  setRongsok() {
    this.props.change("rongsokBruto", this.props.totalRongsok);
    this.props.change("rongsokNetto", this.props.totalRongsok);
    this.props.change("totalBayar", this.props.totalBayar);
  }
  handleSubmitCash(data) {
    var tampung = [];
    var isi = {
      cash_rp: data.cash,
      harga_emas: data.harga,
      cash_netto: data.cashNetto,
    };
    var print = {
      jenis: "TUNAI",
      gross: data.cash,
      harga: data.harga,
      kadar: "",
      netto: parseFloat(data.cashNetto).toFixed(3),
      pkg: "",
      gwpkg: "",
    };
    var sebelum =
      JSON.parse(localStorage.getItem("PrintPembayaranSupplier")) || [];
    sebelum.push(print);
    localStorage.setItem("PrintPembayaranSupplier", JSON.stringify(sebelum));
    tampung.push(isi);
    localStorage.setItem("totalTunaiRp", data.cashNetto);
    localStorage.setItem("alatBayarTunai", JSON.stringify(tampung));
    this.setTotalBayar();
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Disimpan",
      showConfirmButton: false,
      timer: 1500,
    });
  }
  handleSubmitCashRp(data) {
    var isi = {
      cash_rp: data.cash,
      harga_emas: data.harga,
      cash_netto: data.cashNetto,
    };
    var cashTemp = JSON.parse(localStorage.getItem("cashTempRp")) || [];
    cashTemp.push(isi);
    localStorage.setItem("cashTempRp", JSON.stringify(cashTemp));
    var alatBayarTunai =
      JSON.parse(localStorage.getItem("alatBayarTunaiRp")) || [];
    alatBayarTunai.push(isi);
    localStorage.setItem("alatBayarTunaiRp", JSON.stringify(alatBayarTunai));
    this.props.dispatch(setCashTemp());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.change("totalBayarCash", this.props.totalBayarRp);
  }
  handleSubmitTransfer(data) {
    var tampung = [];
    var isi = {
      harga_emas: data.harga,
      transfer_netto: data.transferNetto,
      transfer_rp: data.transfer,
      acc_from: data.asal,
      acc_to: data.tujuan,
      tanggal_transfer: data.tanggal,
    };
    var print = {
      jenis: "TRANSFER",
      gross: data.transfer,
      harga: data.harga,
      kadar: "",
      netto: parseFloat(data.transferNetto).toFixed(3),
      pkg: "",
      gwpkg: "",
    };
    var sebelum =
      JSON.parse(localStorage.getItem("PrintPembayaranSupplier")) || [];
    sebelum.push(print);
    localStorage.setItem("PrintPembayaranSupplier", JSON.stringify(sebelum));
    tampung.push(isi);
    localStorage.setItem("totalTrfRp", data.transferNetto);
    localStorage.setItem("alatBayarTrf", JSON.stringify(tampung));
    this.setTotalBayar();
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Disimpan",
      showConfirmButton: false,
      timer: 1500,
    });
  }
  handleSubmitTransferRp(data) {
    console.log(data);
    var isi = {
      harga_emas: data.harga,
      transfer_netto: data.transferNetto,
      transfer_rp: data.transfer,
      acc_from: data.asal,
      acc_to: data.tujuan,
      tanggal_transfer: data.tanggal,
    };
    var transferTemp = JSON.parse(localStorage.getItem("transferTempRp")) || [];
    transferTemp.push(isi);
    localStorage.setItem("transferTempRp", JSON.stringify(transferTemp));
    var alatBayarTrf = JSON.parse(localStorage.getItem("alatBayarTrfRp")) || [];
    alatBayarTrf.push(isi);
    localStorage.setItem("alatBayarTrfRp", JSON.stringify(alatBayarTrf));
    this.props.dispatch(setTransferTemp());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.change("totalBayarCash", this.props.totalBayarRp);
  }

  setTotalBayar() {
    var rp = localStorage.getItem("totalTunaiRp") || 0;
    var tf = localStorage.getItem("totalTrfRp") || 0;
    var ciok = localStorage.getItem("totalCiok") || 0;
    var rongsok = localStorage.getItem("totalRongsok") || 0;
    var tambahan = this.props.tambahan || 0;
    this.props.change("totalCash", rp);
    this.props.change("totalTransfer", tf);
    this.props.change("totalCiok", ciok);
    this.props.change("totalRongsok", rongsok);
    this.props.change(
      "totalBayar",
      (
        parseFloat(rp) +
        parseFloat(tf) +
        parseFloat(ciok) +
        parseFloat(rongsok) +
        tambahan
      ).toFixed(3)
    );
    this.props.change("totalBayarCash", this.props.totalBayarRp);
  }
  changeInput = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  //   async getHutang(data) {
  //     this.setState({
  //       supplier: data,
  //     });
  //     console.log(data);
  //     AxiosMasterGet(
  //       `trx-supplier/get/hutang-titipan-supplier/${this.props.tanggal}&${data}`
  //     )
  //       .then((res) => {
  //         this.setState({
  //           hutangnya: res.data,
  //         });
  //       })
  //       .then(() => {
  //         let total_netto = this.state.hutangnya
  //           .map((list) => list.hutang_netto)
  //           .reduce((a, b) => a + b, 0);
  //         let total_rp = this.state.hutangnya
  //           .map((list) => list.hutang_rp)
  //           .reduce((a, b) => a + b, 0);
  //         let simpanLocal = this.state.hutangnya.map((list) => {
  //           let data = {
  //             no_faktur: list.no_ref,
  //             keterangan: list.keterangan,
  //             putus_titip: 0,
  //             harga_emas: 900000,
  //             putus_titip_netto: 0,
  //           };
  //           return data;
  //         });
  //         localStorage.setItem("listHutang", JSON.stringify(simpanLocal));
  //         this.props.change("totalHutangRP", total_rp);
  //         this.props.change("totalHutangGr", total_netto);
  //       });
  //   }
  render() {
    return (
      <div className="col-lg-12" style={{ color: "black" }}>
        <div className="col-lg-12">
          <div className="row">
            {this.props.jenisTransaksi === "BARANG" ? (
              <>
                <Suspense fallback={<Skeleton width={"100%"} height={50} />}>
                  <AccordCash
                    onSubmit={(data) => this.handleSubmitCash(data)}
                  />
                  <AccordTrannsfer
                    onSubmit={(data) => this.handleSubmitTransfer(data)}
                  />
                </Suspense>
                {/* CIOK*/}
                <Suspense fallback={<Skeleton width={"100%"} height={50} />}>
                  <AccordCiok
                    totalCiok={this.props.totalCiok}
                    totalBrutoCiok={this.props.totalBrutoCiok}
                  />
                </Suspense>
                {/* RONGSOK */}
                <Suspense fallback={<Skeleton width={"100%"} height={50} />}>
                  <AccordRongsok
                    totalRongsok={this.props.totalRongsok}
                    totalBrutoRongsok={this.props.totalBrutoRongsok}
                  />
                </Suspense>
              </>
            ) : (
              <>
                <Suspense fallback={<Skeleton width={"100%"} height={50} />}>
                  <AccordCashMurni
                    onSubmit={(data) => this.handleSubmitCashRp(data)}
                  />
                  <AccordTransferMurni
                    onSubmit={(data) => this.handleSubmitTransferRp(data)}
                  />
                </Suspense>
              </>
            )}
            {/* PUTUS TITIPAN RP */}
            {/* <AccordTitipanRp /> */}
            {/* PUTUS TITIPAN GR */}
            {/* <AccordTitipanGR /> */}
          </div>
        </div>
        <form onSubmit={this.props.handleSubmit} className="col-lg-12">
          <div className="row">
            <div className="col-md-3 col-lg-3">
              <Field
                name="tanggal"
                label="Tanggal"
                type="date"
                component={renderField}
                defaultValue={this.state.tanggal}
                onChange={this.changeInput("tanggalInvoice")}
              />
            </div>
            <div className="col-md-3 col-lg-3">
              <Field
                name="supplier"
                label="Supplier"
                className="form-control"
                component={renderFieldSelect}
                options={this.props.supplier.map((list) => {
                  let data = {
                    value: list.kode_supplier,
                    name: list.nama_supplier,
                  };
                  return data;
                })}
                defaultValue="DEFAULT | DEFAULT"
                // onChange={(data) => this.getHutang(data)}
              ></Field>
            </div>
            <div className="col-md-3 col-lg-3">
              <Field
                name="noBon"
                label="Bon Titip"
                type="txt"
                component={renderField}
                onChange={this.changeInput("noPembayaran")}
              />
            </div>
            <div className="col-md-3 col-lg-3">
              <Field
                name="detail"
                label="Detail"
                type="text"
                component={renderField}
              />
            </div>

            {/* Row 5 */}
            {this.props.jenisTransaksi === "BARANG" ? (
              <>
                <div className="col-lg-12">
                  <div className="card">
                    <div className="row p-3">
                      <div className="col-lg-4 col-md-4 d-none">
                        <div className="form-group">
                          <Field
                            name="totalCash"
                            label="Denda"
                            type="number"
                            component={renderField}
                          />
                        </div>
                      </div>
                      <div className="col-lg-4 col-md-4 d-none">
                        <div className="form-group">
                          <Field
                            name="totalTransfer"
                            label="Denda"
                            type="number"
                            component={renderField}
                          />
                        </div>
                      </div>
                      <div className="col-lg-4 col-md-4 d-none">
                        <div className="form-group">
                          <Field
                            name="totalCiok"
                            label="Denda"
                            type="number"
                            component={renderField}
                          />
                        </div>
                      </div>
                      <div className="col-lg-4 col-md-4 d-none">
                        <div className="form-group">
                          <Field
                            name="totalRongsok"
                            label="Denda"
                            type="number"
                            component={renderField}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </>
            ) : null}
            {/* Row 5 */}
            <div className="col-lg-12">
              <div className="card">
                <div className="row p-3">
                  <div
                    className={
                      this.props.jenisTransaksi === "BARANG"
                        ? "col-lg-4 col-md-4"
                        : "col-lg-4 col-md-4 d-none"
                    }
                  >
                    <div className="form-group">
                      <Field
                        name="totalBayar"
                        label="Total Titip"
                        type="number"
                        component={renderField}
                      />
                    </div>
                  </div>
                  <div
                    className={
                      this.props.jenisTransaksi !== "BARANG"
                        ? "col-lg-4 col-md-4"
                        : "col-lg-4 col-md-4 d-none"
                    }
                  >
                    <div className="form-group">
                      <label htmlFor="">Total Bayar Rp</label>
                      <Field
                        name="totalBayarCash"
                        label="Total Titip Rp"
                        type="telp"
                        component="input"
                        className="form-control"
                        onBlur={this.props.change(
                          "totalBayarCash",
                          this.props.totalBayarRp
                        )}
                        {...currencyMask}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="row">
                <div className="col-lg-6">
                  <div className="text-left"></div>
                </div>
                <div className="col-lg-6">
                  <div className="text-right">
                    <button
                      className="btn btn-primary"
                      type="submit"
                      disabled={this.props.onSend}
                    >
                      {this.props.onSend ? (
                        <span
                          className="spinner-border spinner-border-sm mr-3"
                          aria-hidden="true"
                        ></span>
                      ) : null}
                      Simpan
                      <i className="fa fa-paper-plane ml-3"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </form>
        <ModalMasterData
          content={
            this.props.tipeBayarModal === "CIOK" ? (
              <Suspense
                fallback={<Skeleton width={"100%"} height={50} count={5} />}
              >
                <FormCiok
                  onSubmit={(data) => this.handleSubmit(data)}
                  from="CK"
                  data={this.props.tampungCiok}
                  onSend={this.props.onSend}
                />
              </Suspense>
            ) : (
              <Suspense
                fallback={<Skeleton width={"100%"} height={50} count={5} />}
              >
                <FormRongsok
                  onSubmit={(data) => this.handleSubmit2(data)}
                  from="RSK"
                  data={this.props.tampungRongsok}
                  onSend={this.props.onSend}
                />
              </Suspense>
            )
          }
          title={
            this.props.tipeBayarModal === "CIOK"
              ? "Tambah Data Ciok"
              : "Tambah Data Rongsok"
          }
        />
      </div>
    );
  }
}

AmbilTitipanSupplierHead = reduxForm({
  form: "AmbilTitipanSupplierHead",
  enableReinitialize: true,
  validate: validasiPembayaranSupplier,
})(AmbilTitipanSupplierHead);
const selector = formValueSelector("AmbilTitipanSupplierHead"); // <-- same as form name
export default connect((state) => {
  const {
    harga,
    cash,
    transfer,
    totalCash,
    totalTransfer,
    totalCiok,
    totalRongsok,
    tanggal,
  } = selector(
    state,
    "harga",
    "cash",
    "transfer",
    "totalCash",
    "totalTransfer",
    "totalCiok",
    "totalRongsok",
    "tanggal"
  );
  var bayarTrfRp = JSON.parse(localStorage.getItem("alatBayarTrfRp"))
    ? JSON.parse(localStorage.getItem("alatBayarTrfRp"))
    : 0;
  var bayarTunaiRp = JSON.parse(localStorage.getItem("alatBayarTunaiRp"))
    ? JSON.parse(localStorage.getItem("alatBayarTunaiRp"))
    : 0;
  var tunaiRp = bayarTunaiRp[0] === undefined ? 0 : bayarTunaiRp[0].cash_netto;
  var transferrRp =
    bayarTrfRp[0] === undefined ? 0 : bayarTrfRp[0].transfer_netto;
  return {
    tanggal: tanggal,
    supplier: state.provinsi.supplier,
    tipeBayarModal: state.provinsi.tipeBayarModal,
    onSend: state.provinsi.onSend,
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
    totalBayar: (
      parseFloat(totalCash) +
      parseFloat(totalTransfer) +
      parseFloat(totalCiok) +
      parseFloat(totalRongsok)
    ).toFixed(3),
    totalBayarRp: parseFloat(tunaiRp) + parseFloat(transferrRp),
    dataHutangSupplier: state.provinsi.dataHutangSupplier,
  };
})(AmbilTitipanSupplierHead);
