import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const PrintNota = (
  row1name = "",
  row1isi = "",
  row2name = "",
  row2isi = "",
  row3name = "",
  row3isi = "",
  row4name = "",
  row4isi = "",
  username = "",
  tanggal = "",
  validby = "",
  tableColumn = [],
  title = "",
  tableRows
) => {
  // initialize jsPDF
  const doc = new jsPDF();
  doc.setProperties({
    title: title,
  });
  // doc.autoTable({
  //   head: tableColumn,
  //   body: tableRows,
  //   startY: 40,
  //   theme: "plain",
  //   rowPageBreak: "avoid",
  //   pageBreak: "avoid",
  //   margin: { top: 20 },
  //   bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
  //   headStyles: {
  //     lineWidth: 0.02,
  //     lineColor: "#000",
  //     fillColor: [212, 212, 211],
  //   },
  // });
  doc.autoTable(tableColumn, tableRows, {
    startY: 40,
    theme: "plain",
    rowPageBreak: "avoid",
    pageBreak: "avoid",
    margin: { top: 20 },
    bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
    headStyles: {
      lineWidth: 0.02,
      lineColor: "#000",
      fillColor: [212, 212, 211],
    },
  });
  let finalY = doc.lastAutoTable.finalY + 10;
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  doc.text(title, 14, 15);
  doc.setFontSize(10);
  //row 1
  doc.text(row1name, 14, 25);
  doc.text(":", 37, 25);
  doc.text(row1isi, 40, 25);
  doc.text(row2name, 120, 25);
  doc.text(":", 141, 25);
  doc.text(row2isi, 145, 25);
  //row 2
  doc.text(row3name, 14, 30);
  doc.text(":", 37, 30);
  doc.text(row3isi, 40, 30);
  doc.text(row4name, 120, 30);
  doc.text(":", 141, 30);
  doc.text(row4isi, 145, 30);

  //Stamp
  doc.text("User", 160, finalY + 10);
  doc.text(":", 168, finalY + 10);
  doc.text(username, 170, finalY + 10);
  doc.text("Cetak", 160, finalY + 15);
  doc.text(":", 168, finalY + 15);
  doc.text(tanggal, 170, finalY + 15);
  doc.text("Valid", 160, finalY + 20);
  doc.text(":", 168, finalY + 20);
  doc.text(validby, 170, finalY + 20);
  // doc.autoPrint();
  // doc.save(`${title}_${dateStr}.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>" +
      title +
      "</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  x.document.close();
};

export default PrintNota;
